<template>
  <div id="ipi-board">
    <div class="left">
      <div class="title">
        <h2>邮件预警</h2>
      </div>
      <div class="left-content">
        <h3>请选择店铺</h3>
        <!-- 菜单 -->
        <el-menu
          default-active="0-0-0"
          class="el-menu-vertical-demo"
          unique-opened
        >
          <el-submenu
            :index="index + ''"
            v-for="(item, index) in menuPlatFormList"
            :key="index"
          >
            <template slot="title">
              <span
                style="display:inline-block;width:100%"
                @click="onceClick(index)"
                >{{ item }}</span
              >
            </template>
            <el-submenu
              :index="index + '-' + index2"
              v-for="(item2, index2) in showCountryList"
              :key="index2"
            >
              <template slot="title">
                <span
                  style="display:inline-block;width:100%"
                  @click="secondClick(index2)"
                  >{{ item2 }}</span
                >
              </template>
              <el-menu-item
                :index="index + '-' + index2 + '-' + index3"
                v-for="(item3, index3) in lastSiteList"
                :key="index3"
                @click="thirdClick(item, item2, item3)"
              >
                <span>{{ item3 }}</span>
              </el-menu-item>
            </el-submenu>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <div class="right">
      <div class="select">
        <span>按问题类型筛选</span>
        <el-select
          v-model="checkedProType"
          placeholder="请选择"
          @change="proTypeChange"
        >
          <el-option
            v-for="(item, index) in proTypeList"
            :key="index"
            :label="item[0]"
            :value="item[0]"
          ></el-option>
        </el-select>
        <el-button type="primary" @click="addProType">新增问题类型</el-button>
        <el-button type="primary" @click="deletePorType"
          >删除问题类型</el-button
        >
        <el-button type="primary" @click="keyword">关键词</el-button>
        <el-button type="primary" @click="audit">问题类型审核</el-button>
      </div>
      <div class="table">
        <el-table
          :data="mailList"
          border
          style="width: 96%;margin: 0 auto;"
          max-height="700"
          ref="VMTable"
          v-loading="loading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
        >
          <el-table-column
            type="index"
            :index="indexMethods"
            width="80px"
            label="序号"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="4"
            align="center"
            label="收件邮箱地址"
            width="150"
          ></el-table-column>
          <el-table-column
            width="350"
            prop="5"
            align="center"
            label="发件人"
          ></el-table-column>
          <el-table-column
            prop="6"
            align="center"
            label="发件时间"
            width="150"
          ></el-table-column>
          <el-table-column
            prop="7"
            align="center"
            label="问题类型"
            width="100"
          ></el-table-column>
          <el-table-column prop="8" align="center" label="邮件内容">
            <template slot-scope="scope">
              <el-popover
                placement="bottom"
                :popper-options="{
                  boundariesElement: 'viewport',
                  removeOnDestroy: true
                }"
                width="600"
                trigger="click"
                :content="scope.row[8]"
              >
                <p class="mail-content" slot="reference">
                  {{ scope.row[8] }}
                </p>
              </el-popover>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total,prev, pager, next, jumper"
          :total="total"
          style="text-align:center;margin-top:10px;"
        ></el-pagination>
      </div>
    </div>
    <!-- 新增问题类型对话框 -->
    <el-dialog
      title="新增问题类型"
      :visible.sync="addProTypeDialogVisible"
      width="30%"
      center
      @close="addProTypeClose"
    >
      <el-form
        :model="addProTypeForm"
        :rules="formRules"
        ref="addProRef"
        label-width="100px"
      >
        <el-form-item label="问题类型" prop="proName">
          <el-input v-model="addProTypeForm.proName"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addProTypeDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addProTypeConfirm">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 删除问题类型对话框 -->
    <el-dialog
      title="删除问题类型"
      :visible.sync="deleteProTypeDialogVisible"
      width="30%"
      center
      @close="deleteProTypeClose"
    >
      <el-form
        :model="deleteProTypeForm"
        :rules="formRules"
        ref="deleteProRef"
        label-width="100px"
      >
        <el-form-item label="问题类型" prop="deleteProName">
          <div v-if="checkProTypeList.length > 0">
            <el-checkbox-group v-model="deleteProTypeForm.deleteProName">
              <el-checkbox
                :label="item[0]"
                v-for="(item, index) in checkProTypeList"
                :key="index"
                >{{ item[0] }}</el-checkbox
              >
            </el-checkbox-group>
          </div>
          <div v-else>目前暂时没有可以选择的问题类型</div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteProTypeDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="deleteProTypeConfirm"
          >确 定</el-button
        >
      </span>
    </el-dialog>

    <!-- 关键词对话框 -->
    <el-dialog
      title="关键词管理"
      :visible.sync="keywordDialogVisible"
      width="30%"
      center
      @close="keywordClose"
    >
      <div class="keyword_dialog">
        <el-form
          :model="keyWordForm"
          :rules="formRules"
          ref="keyWordRef"
          label-width="100px"
        >
          <el-form-item label="问题类型" prop="keyWordProName">
            <el-select
              v-model="keyWordForm.keyWordProName"
              placeholder="请选择"
              @change="proNameChange"
            >
              <el-option
                v-for="(item, index) in proTypeList"
                :key="index"
                :label="item[0]"
                :value="item[0]"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div class="form_body">
          <el-form>
            <el-form-item
              :label="index == 0 ? '已有的关键词' : ''"
              prop="haveKeyWord"
              v-for="(item, index) in keywordList"
              :key="index"
              :class="['flex', { notFirst: index !== 0 }]"
            >
              <el-button
                @click="removeOneKeyWord(index)"
                type="danger"
                size="mini"
                >-</el-button
              >
              <el-input v-model="item[0]"></el-input>
              <!-- <div class="keyword-margin">{{ item[0] }}</div> -->
              <el-button @click="addNewKeyWord" type="primary" size="mini"
                >+</el-button
              >
            </el-form-item>
          </el-form>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="keywordDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="keyWordConfirm">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 问题类型审核 -->
    <!-- <el-dialog :visible.sync="examineProTypeDialogVisible" width="30%" center @close="examineProTypeClose">
      <el-form :model="examineProTypeForm" ref="examineProTypeRef" label-width="140px" :rules="formRules">
        <el-form-item label="未审核的问题类型：" prop="checkIndex">
          <el-radio-group v-model="examineProTypeForm.checkIndex" v-if="auditProTypeList.length>0">
            <el-radio
              :label="index"
              v-for="(item,index) in auditProTypeList"
              :key="index"
            >{{item[0]}}</el-radio>
          </el-radio-group>
          <span v-else>暂无未审核的问题类型</span>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer" v-if="auditProTypeList.length>0">
        <el-button @click="examineProTypeNoConfirm">审核不通过</el-button>
        <el-button type="primary" @click="examineProTypeConfirm">审核通过</el-button>
      </span>
    </el-dialog>-->
    <el-dialog
      :visible.sync="auditDialogVisible"
      width="60%"
      center
      @close="auditClose"
    >
      <el-link
        type="primary"
        @click="(isShowProType = true), (isShowKeyWord = false)"
        style="margin-right:4vw"
        >查看待审核问题类型</el-link
      >
      <el-link
        type="primary"
        @click="(isShowKeyWord = true), (isShowProType = false)"
        >查看待审核关键词</el-link
      >
      <div class="audit-content">
        <!-- 由于下面两个div结构相似，加key值防止出现bug -->
        <!-- 问题类型审核 -->
        <div class="audit-pro-type" v-if="isShowProType" key="protype">
          <!-- <div class="pro-type">
            <span>待审核的问题类型新增：</span>
            <el-radio-group
              v-model="auditAdd.checkedProType"
              v-if="auditProTypeList.length>0"
              style="margin-right:16px"
            >
              <el-radio
                :label="item[0]"
                v-for="(item,index) in auditProTypeList"
                :key="index"
              >{{item[0]}}</el-radio>
            </el-radio-group>
            <span v-else>暂无未审核的问题类型</span>
            <el-link
              type="success"
              style="margin:0 6px 0 6px"
              v-if="auditProTypeList.length>0"
              @click="proTypeAdopt('审核通过')"
            >通过（目前仅支持单选）</el-link>
            <el-link
              type="danger"
              style="margin:0 6px 0 6px"
              v-if="auditProTypeList.length>0"
              @click="proTypeAdopt('审核不通过')"
            >不通过</el-link>
          </div>-->
          <el-table :data="auditProTypeList" style="width: 100%">
            <el-table-column prop="0" label="问题类型"></el-table-column>
            <el-table-column prop="1" label="审核类型"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-link
                  type="success"
                  @click="proTypeAdopt(scope.row[0], '审核通过')"
                  >审核通过</el-link
                >
                <el-link
                  type="danger"
                  @click="proTypeAdopt(scope.row[0], '审核不通过')"
                  >审核不通过</el-link
                >
              </template>
            </el-table-column>
          </el-table>
        </div>
        <!-- 关键词审核 -->
        <div class="audit-keyword" v-if="isShowKeyWord" key="keyword">
          <el-table :data="auditKeyWordList" style="width: 100%">
            <el-table-column prop="0" label="问题类型" fixed></el-table-column>
            <el-table-column prop="1" label="关键词"></el-table-column>
            <el-table-column prop="2" label="审核类型"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-link
                  type="success"
                  @click="keyWordAdopt(scope.row[0], scope.row[1], '审核通过')"
                  >审核通过</el-link
                >
                <el-link
                  type="danger"
                  @click="
                    keyWordAdopt(scope.row[0], scope.row[1], '审核不通过')
                  "
                  >审核不通过</el-link
                >
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      loading: false,
      list: ["a", "b", "c", "d"],
      // 左侧导航栏菜单
      menuPlatFormList: [],
      menuCountryList: [],
      menuSiteList: [],
      showCountryList: [],
      showSiteList: [],
      lastSiteList: [],

      // 邮件列表
      mailList: [],
      // 选中的菜单项
      checkedPlatform: "",
      checkedCountry: "",
      checkedSite: "",
      // 选中的问题类型
      checkedProType: "VAT",

      // 问题类型列表（包含已审核与删除未审核的）
      proTypeList: [],
      // 已审核的问题类型列表
      checkProTypeList: [],
      // 待审核的问题类型(新增待审核/删除待确认,关键词为空)
      auditProTypeList: [],
      // 待审核的关键词
      auditKeyWordList: [],

      formRules: {
        proName: [
          { required: true, message: "请输入问题类型", trigger: "blur" }
        ],
        // newKeyWord:[
        //   { required: true, message: "请输入新增的", trigger: "blur" }
        // ],
        deleteProName: [
          { required: true, message: "请选择问题类型", trigger: "change" }
        ],
        keyWordProName: [
          { required: true, message: "请选择问题类型", trigger: "change" }
        ]
      },
      // 新增问题类型对话框
      addProTypeDialogVisible: false,
      addProTypeForm: {
        proName: ""
      },

      // 删除问题类型对话框
      deleteProTypeDialogVisible: false,
      deleteProTypeForm: {
        deleteProName: []
      },
      // 关键词管理
      keywordDialogVisible: false,
      keyWordForm: {
        keyWordProName: "",
        newKeyWord: ""
      },
      // 关键词列表
      keywordList: [],
      // 是否显示新增对话框
      isShowNewKeyWord: false,
      // 新增的对话框数量
      addInputList: [],

      // 问题类型审核
      auditDialogVisible: false,
      // 审核新增相关
      auditAdd: {
        // 选中的待审核新增问题类型
        checkedProType: ""
      },
      // 显示新增的审核还是删除的审核
      isShowKeyWord: false,
      isShowProType: false,
      // 分页相关
      currentPage: 1,
      total: 0,
      pageSize: 50
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取菜单栏数据
    getMenuList() {
      this.$api.getMenuList().then(res => {
        // console.log(res);
        this.menuPlatFormList = res.data.data1;
        this.menuCountryList = res.data.data2;
        this.menuSiteList = res.data.data3;
        // 页面刷新 先默认展示胤佑的 同时在导航那里添加default-active="0-0-0"
        this.showCountryList = this.menuCountryList[0];
        this.showSiteList = this.menuSiteList[0];
        this.lastSiteList = this.showSiteList[0];
        this.checkedPlatform = this.menuPlatFormList[0];
        this.checkedCountry = this.showCountryList[0];
        this.checkedSite = this.lastSiteList[0];
        this.thirdClick(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite
        );
      });
    },
    // 获取问题类型下拉框数据（已审核与删除待审核的都在内）
    getProTypeList() {
      this.$api.proTypeList().then(res => {
        console.log(res);
        this.proTypeList = this.$utils.deepClone(res.data.data);
        // 需要去重
        for (let i = 0; i < this.proTypeList.length; i++) {
          for (let j = i + 1; j < this.proTypeList.length; j++) {
            if (this.proTypeList[i][0] === this.proTypeList[j][0]) {
              this.proTypeList.splice(j, 1);
              j--;
            }
          }
        }
        console.log("aaaa", this.proTypeList);
      });
    },
    // 获取问题类型，只有已审核的
    getCheckProTypeList() {
      this.$api.getCheckProTypeList().then(res => {
        // console.log(res);
        this.checkProTypeList = res.data.data;
      });
    },
    // 获取待审核的问题类型（新增待审核/删除待确认,关键词为空）
    getAuditTypeList() {
      this.$api.getAuditTypeList().then(res => {
        // console.log(res);
        this.auditProTypeList = res.data.type_list;
        this.auditKeyWordList = res.data.keyword_list;
      });
    },
    onceClick(index) {
      this.showCountryList = [];
      this.showSiteList = [];
      this.showCountryList = this.menuCountryList[index];
      this.showSiteList = this.menuSiteList[index];
    },
    secondClick(index) {
      // console.log(index);
      this.lastSiteList = [];
      this.lastSiteList = this.showSiteList[index];
    },
    thirdClick(platform, country, site) {
      this.loading = true;
      this.checkedPlatform = platform;
      this.checkedCountry = country;
      this.checkedSite = site;
      this.checkedProType = "VAT";
      let page = 1;
      this.$api
        .queryMail(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          this.checkedProType,
          page
        )
        .then(res => {
          console.log(res);
          this.mailList = res.data.data;
          this.total = res.data.all_number;
          this.$nextTick(() => {
            this.$refs["VMTable"].bodyWrapper.scrollTop = 0;
          });
          this.currentPage = 1;
          this.loading = false;
        });
    },
    proTypeChange(val) {
      this.loading = true;
      this.checkedProType = val;
      let page = 1;
      this.$api
        .queryMail(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          this.checkedProType,
          page
        )
        .then(res => {
          // console.log(res);
          this.mailList = res.data.data;
          this.total = res.data.all_number;
          this.$nextTick(() => {
            this.$refs["VMTable"].bodyWrapper.scrollTop = 0;
          });
          this.currentPage = 1;
          this.loading = false;
        });
    },
    // 下拉框联合
    selectChange(val) {
      console.log(val);
    },
    // 新增问题类型对话框
    addProType() {
      this.$nextTick(() => {
        this.addProTypeDialogVisible = true;
      });
    },
    addProTypeClose() {
      this.$refs.addProRef.resetFields();
    },
    addProTypeConfirm() {
      this.$refs.addProRef.validate(state => {
        if (!state) return;
        let formData = new FormData();
        formData.append("alert_type", this.addProTypeForm.proName);
        this.$api.addProType(formData).then(res => {
          // 修改：判断类型是否存在
          if (res.data.code === 4041) {
            this.$message.warning(res.data.msg);
            return;
          }
          // console.log(res);
          this.$message.success("新增成功,等待主管审核");
          this.addProTypeDialogVisible = false;
          this.getAuditTypeList();
        });
      });
    },
    // 删除问题类型
    deletePorType() {
      this.deleteProTypeDialogVisible = true;
    },
    deleteProTypeClose() {
      this.$refs.deleteProRef.resetFields();
    },
    deleteProTypeConfirm() {
      this.$refs.deleteProRef.validate(state => {
        if (!state) return;
        // console.log(this.deleteProTypeForm.deleteProName);
        // 给删除的列表赋值
        // this.auditProTypeList = this.deleteProTypeForm.deleteProName || [];
        console.log("删除的列表", this.deleteProTypeForm.deleteProName);
        console.log("删除的数据", this.auditProTypeList);
        // 遍历数据
        for (let key in this.deleteProTypeForm.deleteProName) {
          let value = this.deleteProTypeForm.deleteProName[key];
          let arr = [];
          arr.push(value);
          arr.push("删除类型待审核");
          this.auditProTypeList.push(arr);
        }
        console.log("删除的数据", this.auditProTypeList);
        this.$api
          .deleteProType(this.deleteProTypeForm.deleteProName)
          .then(res => {
            // console.log(res);
            this.$message("删除成功，等待主管审核");
            // 问题类型选中第一个
            this.checkedProType =
              this.proTypeList.length > 0 ? this.proTypeList[0][0] : "";
            this.deleteProTypeDialogVisible = false;
          });
      });
    },
    // 关键词管理
    keyword() {
      this.keywordDialogVisible = true;
    },
    keywordClose() {
      this.$refs.keyWordRef.resetFields();
      this.isShowNewKeyWord = false;
      this.addInputList = [];
      this.keywordList = [];
    },
    keyWordConfirm() {
      this.$refs.keyWordRef.validate(state => {
        if (!state) return;

        if (
          this.keywordList.length === 1 &&
          this.keywordList[0][0] === null &&
          this.addInputList.length === 0
        )
          return;
        console.log(this.keywordList);
        console.log(this.addInputList);
        // let count = 0;
        let formData = new FormData();
        formData.append("alert_type", this.keyWordForm.keyWordProName);
        for (let item of this.keywordList) {
          // if (item[0] === null) {
          //   continue;
          // }
          // count++;
          formData.append("keyword_list", item[0]);
        }
        // if (count === 0){
        //   this.$message.warning('请');
        // };
        for (let item of this.addInputList) {
          if (item != "") {
            formData.append("keyword_list", item);
          }
        }

        this.$api.editKeyWord(formData).then(res => {
          console.log(res);
          this.$message.success("修改关键词成功,等待管理员审核");
          this.getAuditTypeList();
          this.keywordDialogVisible = false;
        });
      });
    },
    // 移除一个关键词
    removeOneKeyWord(index) {
      // console.log(index);
      if (this.keywordList.length === 1) return;
      for (let i in this.keywordList) {
        if (i == index) {
          this.keywordList.splice(i, 1);
        }
      }
      console.log(this.keywordList);
    },
    // 新增一个关键词
    addNewKeyWord() {
      let len = this.addInputList.length - 1;
      for (let key in this.keywordList) {
        let keywordItem = this.keywordList[key];
        if (keywordItem[0] === null) {
          return;
        }
      }
      // if (this.keywordList[0][0] === null || this.addInputList[len] === "") {
      //   return;
      // }
      this.isShowNewKeyWord = true;
      let item = [null];
      this.keywordList.push(item);
      console.log(this.keywordList);
      // this.addInputList.push("");
    },
    // 关键词管理中，问题列表被选择触发
    proNameChange(val) {
      // console.log(val);
      this.$api.getKeyWord(val).then(res => {
        console.log(res);
        this.keywordList = res.data.data;
        console.log("获取的数据", this.keywordList);
        if (this.keywordList.length > 1 && this.keywordList[0][0] === null) {
          this.keywordList.shift();
          console.log(this.keywordList);
        }
      });
      this.addInputList = [];
    },

    // 审核问题类型
    audit() {
      this.auditDialogVisible = true;
    },
    auditClose() {
      this.isShowProType = false;
      this.isShowKeyWord = false;
    },
    // 问题类型审核通过与不通过
    proTypeAdopt(type, audit) {
      this.$api.audit(type, "", audit).then(res => {
        // console.log(res);
        this.$message.success("审核成功");
        // 取消自动关闭弹窗
        // this.auditDialogVisible = false;
        // 更新待审核的问题类型
        this.getAuditTypeList();
        // 更新问题类型下拉框数据
        this.getProTypeList();
        // 更新只有已审核的问题类型
        this.getCheckProTypeList();
      });
    },
    // 关键词审核通过与不通过
    keyWordAdopt(type, keyword, audit) {
      this.$api.audit(type, keyword, audit).then(res => {
        // console.log(res);
        this.$message.success("审核成功");
        this.getAuditTypeList();

        // this.auditDialogVisible = false;
      });
    },
    // 分页
    handleCurrentChange(val) {
      this.loading = true;
      console.log(val);
      this.$api
        .queryMail(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          this.checkedProType,
          val
        )
        .then(res => {
          // console.log(res);
          this.mailList = res.data.data;
          this.total = res.data.all_number;
          this.$nextTick(() => {
            this.$refs["VMTable"].bodyWrapper.scrollTop = 0;
          });
          this.loading = false;
        });
    },
    // 序号
    indexMethods(index) {
      return (this.currentPage - 1) * 50 + (index + 1);
    }
  },
  created() {
    this.getMenuList();
    this.getProTypeList();
    this.getCheckProTypeList();
    this.getAuditTypeList();
  },
  mounted() {}
};
</script>
<style>
.flex .el-form-item__content {
  display: flex;
}
.el-popover {
  max-height: 60vh;
  overflow: scroll;
  overflow-x: hidden;
}
#ipi-board .el-dialog .el-button + .el-input {
  width: 150px;
}
#ipi-board .el-table .cell {
  padding: 0;
}
#ipi-board .el-dialog .keyword_dialog {
  margin: 0 auto;
  text-align: center;
  width: 425px;
}

#ipi-board .el-dialog .form_body {
  max-height: 300px;
  overflow-y: auto;
  overflow-x: hidden;
}
#ipi-board .el-dialog .form_body .el-form {
  width: 480px;

  margin-left: 32px;
}
#ipi-board .keyword_dialog .notFirst {
  margin-left: 97px;
}
#ipi-board .keyword_dialog .el-form:first-child {
  margin-left: 27px;
  text-align: left;
}
#ipi-board .el-dialog .keyword_dialog .el-form {
  margin-left: 30px;
}
</style>
<style scoped>
#ipi-board {
  width: 100vw;
  height: 92vh;
  position: relative;
}
#ipi-board .select {
  margin-bottom: 22px;
}
#ipi-board .select > span {
  vertical-align: -2px;
}
#ipi-board .select .el-select {
  margin: 0 10px;
}

#ipi-board h2 {
  text-align: center;
}
#ipi-board .el-popover {
  color: red;
}
.left {
  position: absolute;
  width: 16vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  border-right: 1px solid black;
  top: 0;
  left: 0;
  overflow: auto;
}
.title {
  margin-top: 2vh;
}
.right {
  position: absolute;
  top: 0;
  left: 18vw;
  width: 80vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 24px;
  overflow: auto;
}
.left-content h3 {
  margin-top: 2vh;
  text-align: center;
}
.mail-content {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
}
.keyword-margin {
  margin: 0 6px 0 6px;
}
.audit-content {
  height: 40vh;
  margin-top: 6vh;
}
</style>



